form {
    width: 90%;
    max-width: 300px;
    background-color: #f1f1f1;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: #1E1E1F;
    margin-bottom: 26px;
}

textarea{
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 5px;
    box-shadow: 0px 5px 50px rgba (32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);

}

textarea::placeholder{
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.Formulario-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.Formulario-button-add{
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba (97, 218, 250, 0.25);
}

.Formulario-button-cancel {
    background: transparent;
    box-shadow: 0px 5px 25px rgba (97, 218, 250, 0.25);
}